<template>
  <div>
    <div style="display:flex;justify-content:center;">
      <input type="search"
             results="5"
             name="word"
             id="Search"
             placeholder="请输入关键字"
             v-model="searchValue"
             @keyup.enter="searchAnimation"
             class="input_border" />
      <div class="search"
           style="border-radius: 0 20px 20px 0;"
           @click="Search()">搜索</div>
    </div>
    <div>
      <mt-navbar v-model="selected"
                 @click.native="chooseSelected">
        <mt-tab-item id="2">
          <span>审批通过</span>
        </mt-tab-item>
        <mt-tab-item id="0">
          <span>审批中</span>
        </mt-tab-item>
        <mt-tab-item id="1">
          <span>审批未通过</span>
        </mt-tab-item>
      </mt-navbar>
      <!-- 申请列表 -->
      <div v-if="hotelList.length > 0"
           class="scroll">
        <van-pull-refresh v-model="pullLoading"
                          @refresh="onPullRefresh"
                          success-text="刷新成功">
          <van-list v-model="listLoading"
                    @load="onLoadList"
                    :offset="30"
                    :finished="finishedList"
                    finished-text="没有更多了">
            <div v-for="(item, i) in hotelList"
                 :key="i"
                 style="background:#fff;padding:10px 0;margin-bottom:10px;">
              <div class="info-hotel">
                <div class="hotel-left">
                  <div style="width:80px;margin-right:8px;">
                    <img :src="item.domainName + item.frontDoorImage"
                         class='left-img'>
                  </div>
                  <div class="left-title">
                    <div style="display:flex;align-items:center;">
                      <div class="c_000">{{ item.hotelName }}</div>
                    </div>
                    <div class="adr">{{ item.location || '暂无' }}</div>
                    <div style="font-size:0.7rem;color:#888;">业务员：{{ item.businessManager.realName || '暂无' }}
                    </div>
                  </div>
                </div>
                <div v-if="selected == '2'"
                     class="hotelStatus"
                     :class="'hotelStatus'+item.hotelStatus">{{ hotelStatusObj[item.hotelStatus] }}</div>
                <div v-else
                     class="hotel-right">{{ selected == '0'?'审批中':'驳回' }}</div>
              </div>
              <div style="display:flex;justify-content:flex-end;">
              </div>
            </div>
          </van-list>
        </van-pull-refresh>
      </div>
      <div v-else
           class="nothing">
        <img src="@/assets/hotelAudit/empty.png"
             alt="">
      </div>
    </div>
  </div>
</template>
<script>
import { PostBusinessHotelJoinBsmList } from '@/api/api_join';
import { GetSystemDictDataByType } from '@/api/api_query';
export default {
    components: {},
    data() {
        return {
            listLoading: false, // 上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, // 上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            hotelList: [], // 列表数据
            dataForm: {
                pageNum: 1,
                pageSize: 10,
                hotelStatus: 2,
                businessManId: this.$route.query.userId ? this.$route.query.userId : '',
                searchValue: null
            },
            searchValue: null,
            selected: '2',
            hotelStatusObj: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '客户管理');
        this.getType();
    },
    methods: {
        getType() {
            GetSystemDictDataByType({ dictType: 'hotel_status' }).done((res) => {
                this.hotelStatusObj = {};
                res.data.map((item) => {
                    this.hotelStatusObj[item.dictValue] = item.dictLabel;
                });
                this.onLoadList();
            });
        },
        searchAnimation() {
            this.dataForm.searchValue = this.searchValue;
            this.hotelList = [];
            this.onLoadList();
        },
        Search() {
            this.dataForm.searchValue = this.searchValue;
            this.hotelList = [];
            this.onLoadList();
        },
        chooseSelected() {
            this.dataForm.hotelStatus = this.selected;
            this.hotelList = [];
            this.dataForm.pageNum = 1;
            this.onLoadList();
        },
        onPullRefresh() {
            this.hotelList = [];
            this.dataForm.pageNum = 1;
            this.finishedList = false;
            this.onLoadList();
        },
        onLoadList() {
            PostBusinessHotelJoinBsmList(this.dataForm).done((res) => {
                this.hotelList = this.hotelList.concat(res.data);
                this.listLoading = this.pullLoading = false;
                if (res.total == this.hotelList.length) {
                    this.finishedList = true;
                } else if (res.total > this.hotelList.length) {
                    this.finishedList = false;
                }
            });
            this.dataForm.pageNum++;
        },
    }
};
</script>
<style scoped>
input {
    -webkit-appearance: none !important;
    border-radius: 20px 0 0 20px !important;
    text-indent: 2em;
    margin: 10px 0;
    width: 82%;
    height: 32px;
    border: 0;
    outline: none;
    box-sizing: border-box;
}
#Search::-webkit-search-cancel-button {
    display: none;
}
.input_border {
    font-size: 0.7rem;
}
.search {
    border: none;
    font-size: 0.7rem;
    color: #fff;
    margin-top: 10px;
    display: inline-block;
    width: 15%;
    height: 32px;
    line-height: 32px;
    background-color: #ff7d41;
}
.search:active {
    opacity: 0.5;
}
.scroll {
    margin: 10px 0;
    overflow: auto;
    position: fixed;
    bottom: 0;
    top: 150px;
    right: 0;
    left: 0;
}
.nothing {
    margin: 100px auto;
    line-height: 50px;
    font-size: 0.8rem;
}
.info-hotel {
    font-size: 0.8rem;
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    color: #b2b2b2;
}
.hotel-left {
    display: flex;
    align-items: center;
    text-align: left;
    width: 80%;
}
.left-img {
    width: 80px;
    height: 80px;
    border-radius: 4px;
}
.left-title {
    height: 86px;
}
.hotel-right {
    color: #ff7d41;
}
.hotelStatus {
    line-height: 20px;
    height: 20px;
    border-radius: 5px;
    padding: 0px 4px;
    font-size: 0.6rem;
}
.hotelStatus0 {
    color: #8c898c;
    border: 1px solid #d1ced1;
}
.hotelStatus1 {
    color: #10b571;
    border: 1px solid #9ddfc4;
}
.hotelStatus2 {
    color: #8c898c;
    border: 1px solid #d1ced1;
}
.hotelStatus3 {
    color: #fe7f4c;
    border: 1px solid #fdc7be;
}
/* 驳回原因 */
.reason {
    color: #000;
    font-size: 0.7rem;
    text-align: left;
    padding: 10px;
    line-height: 20px;
}
.reason-right {
    display: flex;
    justify-content: space-between;
    text-align: center;
}
.edit-reason {
    padding: 4px 8px;
    border: 1px solid #ff7d41;
    border-radius: 2px;
    color: #ff7d41;
    font-size: 0.6rem;
    margin: 0 6px;
}
.c_000 {
    color: #000;
    font-size: 0.9rem;
    max-width: 90px;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.c_f60 {
    color: #ff7d41;
}

.c_ce {
    color: #cecece;
}
.adr {
    font-size: 0.6rem;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    height: 46%;
    margin: 2px 0;
}
/deep/ .mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    border-bottom: 3px solid #ff7d41;
}
</style>



