import * as API from './';
import {
  data
} from 'autoprefixer';
/*********************************************  酒店加盟  *******************************************/
// 分页获取酒店列表-首页
export const PostBusinessHotelJoinList = data => API.POST(`/api/business/hotel/join/list`, data, true)
// 酒店加盟审批通过
export const PostBusinessHotelJoinApplyAgree = data => API.POST(`/api/business/hotel/join/apply/agree`, data, true)
// 酒店加盟审批拒绝
export const PostBusinessHotelJoinApplyRefuse = data => API.POST(`/api/business/hotel/join/apply/refuse`, data, true)
// 分页查询业务部门酒店列表
export const PostBusinessHotelJoinBsmList = data => API.POST(`/api/business/hotel/join/bsm/list`, data, true)  



